@import url(https://fonts.googleapis.com/css2?family=Poppins);
@-webkit-keyframes opacity{from{opacity:0}to{opacity:1}}@keyframes opacity{from{opacity:0}to{opacity:1}}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font:inherit;font-size:100%;font-weight:inherit;font-family:"Libre Baskerville",serif}article,aside,details,figcaption,figure,footer,hgroup,menu,nav,header,section{display:block}*{-webkit-tap-highlight-color:rgba(0,0,0,0)}html{scroll-behavior:smooth}@media(max-width: 1000px){html{scroll-behavior:auto}}body{line-height:1;color:#101010;font-family:serif}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}img{border:none}section{width:100%;padding:0;margin:0}input{background-color:transparent;color:#101010;outline:none;background:none;display:inline-block}a{transition:all .15s linear;color:#333;text-decoration:none}a:hover{transition:all .15s linear;color:#101010}strong,b{font-weight:bold !important}i{font-style:italic !important}button{border:none;outline:none;border-radius:6px}
body{background-color:#ffe3de}
.card{background-color:#9e9d89;margin-top:50px;margin:20px;width:20%;border-radius:16px}.card img{margin:18px 22px}.card p{color:#fff;padding-bottom:10px}
.form-container{display:-webkit-flex;display:flex;-webkit-flex-flow:column;flex-flow:column;-webkit-justify-content:center;justify-content:center;height:350px;-webkit-align-items:center;align-items:center}.form{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.form-div-input{width:300px;height:30px;border-radius:8px;box-shadow:4px 4px 6px rgba(0,0,0,.15);background-color:#fff;padding-left:12px;border:none}.form-div-btn{margin-left:20px;outline:none !important;border-radius:8px;background-color:#b67162;color:#fff;font-weight:600;height:34px;border:none;box-shadow:4px 4px 6px rgba(0,0,0,.15)}
.formhome{width:100%}
.footer{font-size:24px;background-color:#9e9d89;height:72px;padding-top:36px;position:fixed;bottom:0;width:100%;color:#fff;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;font-family:sans-serif}.footer span{opacity:.55}
